<template>
    <div>
        <nav>
            <ul class="nav">
                <router-link
                    to="/about"
                    @mouseenter.native="enter(1)"
                    @mouseleave.native="leave(1)"
                >
                    <li>公司简介</li>
                    <img src="../../assets/nav/bottom.png" alt>
                    <div class="about" v-show="about">
                        <div @click.stop.prevent="routergo('/about')">关于我们</div>
                        <div @click.stop.prevent="routergo('/about/licence')">公司证照</div>
                        <div @click.stop.prevent="routergo('/about/cooperation')">合作品牌</div>
                    </div>
                </router-link>
                <router-link to="/answers">
                    <li>装修问题</li>
                </router-link>
                <router-link to="/casestyle" @mouseenter.native="enter(2)" @mouseleave.native="leave(2)">
                    <li>风格案例（全景）</li>
                    <img src="../../assets/nav/bottom.png" alt>
                    <div class="about about1" v-show="about1">
                        <div @click.stop.prevent="routergo('/about/cooperation')">3D全景图集</div>
                        <div @click.stop.prevent="routergo('/about/cooperation')">平面效果图集</div>
                        <div @click.stop.prevent="routergo('/casestyle')">风格介绍</div>
                        <div @click.stop.prevent="routergo('/about/cooperation')">测试你喜欢的风格</div>
                    </div>
                </router-link>
                <!-- 两种方法 -->
                <router-link to="/test">
                    <li>量房预约（免费）</li>
                </router-link>
                <router-link to="/test" @mouseenter.native="enter(3)" @mouseleave.native="leave(3)">
                    <li>设计</li>
                    <img src="../../assets/nav/bottom.png" alt>
                    <div class="about about1" v-show="about2">
                        <div>设计下单和介绍</div>
                        <div>设计师</div>
                    </div>
                </router-link>
                <router-link to="/test" @mouseenter.native="enter(4)" @mouseleave.native="leave(4)">
                    <li>施工</li>
                    <img src="../../assets/nav/bottom.png" alt>
                    <div class="about about1" v-show="about3">
                        <div>施工下单和介绍</div>
                        <div>施工长</div>
                        <div>现场远程监控</div>
                    </div>
                </router-link>
                <router-link to="/test">
                    <li>精装套餐</li>
                </router-link>
                <router-link to="/test">
                    <li>装饰商城</li>
                </router-link>
                <router-link to="/test">
                    <li>进入我家</li>
                </router-link>
                <button class="back" @click="back()">返回上级</button>
            </ul>
        </nav>
        <router-view></router-view>
        <footbox></footbox>
    </div>
</template>

<script>
import foot from "@/components/utils/foot";
export default {
    components: {
        footbox: foot
    },
    data() {
        return {
            // 关于的隐藏
            about: false,
            // 风格案例
            about1: false,
            // 设计
            about2: false,
            // 施工
            about3: false
        };
    },
    methods: {
        back() {
            this.$router.go(-1); //返回上一层
        },
        // 鼠标移入显示 显示隐藏子导航
        enter(a) {
            // console.log(a)
            if (a == 1) {
                this.about = true;
            } else if (a == 2) {
                this.about1 = true;
            } else if (a == 3) {
                this.about2 = true;
            } else if (a == 4) {
                this.about3 = true;
            }
        },
        // 鼠标移除
        leave(a) {
            // console.log(a)
            if (a == 1) {
                this.about = false;
            } else if (a == 2) {
                this.about1 = false;
            } else if (a == 3) {
                this.about2 = false;
            } else if (a == 4) {
                this.about3 = false;
            }
        },
        // about页面跳转子页面
        routergo(a) {
            this.$router.push(a);
        },
        weibu() {
            // 尾部
        }
    },
    mounted() {}
};
</script>

<style scoped>
nav {
    background: #000;
    color: #fff;
}
.nav {
    width: 1200px;
    margin: 0 auto;
    text-align: left;
    height: 46px;
}
.nav a {
    float: left;
    font-size: 16px;
    color: #fff;
    display: inline-block;
    height: 46px;
    line-height: 46px;
    padding: 0 15px;
    position: relative;
    margin: 0;
    z-index: 10;
}
.nav > a:hover {
    background: rgb(20, 183, 46);
}
.nav a img {
    vertical-align: text-top;
    zoom: 1;
}
.router-link-active {
    /* color: red!important; */
    background: #1fcf3e;
    font-weight: bold;
}
.back {
    transition: 0.1s;
    display: inline-block;
    background: #ffed00;
    font-size: 16px;
    color: #333;
    width: 96px;
    height: 32px;
    line-height: 32px;
    border-radius: 8px;
    margin-top: 6px;
    /* float: right; */
    margin-left: 50px;
    text-align: center;
    border: 1px solid #ffed00;
    font-weight: bold;
    /* 去除button按钮效果 */
    outline: none;
    border-color: transparent;
    box-shadow: none;
    float: right;
}
.back:hover {
    border: 1px solid #aa9f01;
    /* 移入变手 */
    cursor: pointer;
}
/* 隐藏的 */
.about {
    /* display: none; */
    position: absolute;
    left: 0;
    /* display: inline-block; */
    /* height: 46px; */
    line-height: 40px;
    /* padding: 0 15px; */
}
.about div {
    display: inline-block;
    padding: 0 24px;
    width: 116px;
    background: #000;
    height: 40px;
    border-top: 1px solid #fff;
    text-align: center;
}
.about div:hover {
    background: rgb(20, 183, 46);
}
/* 全景 */
.about1 div {
    width: 180px;
}
/* 设计 */
</style>



